import React from 'react';
import ReactDOM from 'react-dom';
import {HashRouter as Router, Route, Link} from 'react-router-dom';
import Parser from 'html-react-parser';
import './index.css';
import './normalize.css';
import myPhoto from './myPhoto.jpg';
import nsysuLogo from './nsysu_logo.png';
import viaLogo from './VIA_logo.jpg';

class Toptitle extends React.Component{
	render(){
		return(
			<div className="top-bg">
				<div className="black-bg">				
					<div id="title" >
						<h1><span>Leon Kuo's</span>Works Collection</h1>
						<h5>簡歷 <span>與</span> 作品集</h5>
					</div>
					<a href="https://github.com/iamkyc" target="_blank" rel="noopener noreferrer">
						<button id="github-button">
							<div id="github-button-icon"><i className="fa fa-github fa-4x" aria-hidden="true"></i></div>
							<div id="github-button-title">View my <span>Github</span></div>
						</button>
					</a>
				</div>
			</div>
		);

	}
}

class Navbar extends React.Component{

	openNav(){
		if(window.screen.width<=992){
			document.getElementById("navbar-content").style.width = "250px";
		}
	}

	closeNav(){
		if(window.screen.width<=992){
			document.getElementById("navbar-content").style.width = "0px";
		}
	}

	render(){
		
		return(
			<div>
				<button id="menu-button" onClick={() => this.openNav()}><i className="fa fa-bars" aria-hidden="true"></i></button>
				<nav id="navbar-content">
					<button id="menu-button-close" onClick={() => this.closeNav()}><i className="fa fa-times fa-2x" aria-hidden="true"></i></button>
					<ul>
						<li className="profileLink" onClick={() => this.closeNav()}>
							<Link to="/">
								<img src={myPhoto} alt=""/>
								<h4>郭彥志</h4>
								<h5>網 頁 前 端 設 計</h5>
							</Link>
						</li>
						<li className="subNav" onClick={() => this.closeNav()}><Link className="navLink" to="/SideProjects"><h5>副專案</h5><p>Side Projects</p></Link></li>
						<li className="subNav" onClick={() => this.closeNav()}><Link className="navLink" to="/Memo"><h5>筆記</h5><p>memo</p></Link></li>
						<li className="subNav" onClick={() => this.closeNav()}><Link className="navLink" to="/Experiences"><h5>經歷</h5><p>Experiences</p></Link></li>
					</ul>
				</nav>
			</div>	
		);

	}
}

class Home extends React.Component{
	render(){
		return(
			<div id="home">
				<div className="row justify-content-center">
					<div className="col-12 col-lg-11">
						<div className="home-myName">
							<h1>郭彥志<span> Yen-Chih Kuo(Leon)</span></h1>
							<p><span>Github</span> iamkyc</p>
							<p><span>Age</span> 25</p>

						</div>
						
						<div className="home-content">
							<h3>學歷</h3>
							<ul>
								<li>國立中山大學 資訊管理學系</li>
								<li>國立中山大學 資訊管理學研究所</li>
							</ul>
						</div>
						
						<div className="home-content">
							<h3>技術</h3>
							<ul>
								<li>前端：html、css/scss、JavaScript(ES6)、BootStrap、React.js、RWD、Webppack</li>
								<li>後端：PHP、SQL</li>
							</ul>		
						</div>
					</div>
				</div>		
			</div>
		);
	}
}

class SideProject extends React.Component{
	render(){
		const tags=this.props.tags;
		let tagSplit=tags.split(" ");
		let htmlSpan=""
		
		for(let split of tagSplit){
			htmlSpan+=`<span>${split}</span>`;
		}

		const webpageURL=this.props.webpageURL;
		const repoURL=this.props.repoURL;
		let button="";


		if(webpageURL==="" || webpageURL==="none"){
			
		} else{
			button+=`<a href=${webpageURL} target="_blank" rel="noopener noreferrer"><button className="sub-button"><i className="fa fa-window-maximize" aria-hidden="true"></i> webpage</button></a>`;
		}
		

		if(repoURL==="" || repoURL==="none"){
			
		} else{
			button+=`<a href=${repoURL} target="_blank"  rel="noopener noreferrer"><button className="sub-button"><i className="fa fa-archive" aria-hidden="true"></i> repo</button></a>`;
		}
	
		return(
			<div className="project row-content">
				<div className="row">
					<div className="col-xl-9">
						<h2>{this.props.chTitle}{Parser(htmlSpan)}</h2>
						<h5>{this.props.enTitle}</h5>
						<p>{this.props.chText}</p>
						<p>{this.props.enText}</p>
					</div>
					<div className="col-xl-3 project-button-area">
						<div className="project-buttons">
							{Parser(button)}
						</div>
						<div className="date">{this.props.date}</div>
					</div>
				</div>
			</div>
		);
	}
}

