class SideProjectsArea extends React.Component{
	render(){
		return(
			<div>
				<div className="sub-title">
					<h1>副專案<span>Side Projects</span></h1>
				</div>
				<SideProject chTitle="台鐵月台電子看板產生器"
				enTitle="Taiwan-Railway-Train-Platform-Electronic-Billboards-Generator"
				chText="仿造台鐵月台的電子看板，製作輸入框來編輯月台螢幕內容。"
				enText="Imitate the electronic billboard of the Taiwan Railway's platform, and create the input boxes to edit the screen content."
				tags="ReactJS SCSS ES6"
				webpageURL="https://iamkyc.github.io/Taiwan-Railway-Train-Platform-Electronic-Billboards-Generator/"
				repoURL="https://github.com/iamkyc/Taiwan-Railway-Train-Platform-Electronic-Billboards-Generator"
				date="2017-10-12"
				/>
				<SideProject chTitle="簡歷與作品集"
				enTitle="Simple-Resume-And-Works-Collection"
				chText="個人簡歷網頁，以及介紹自己的作品(含公司作品與github作品)"
				enText="Personal resume page, and introduce my own works (including company works and github works)."
				tags="ReactJS SCSS ES6 Webpack Bootstrap RWD"
				webpageURL="https://iamkyc.github.io/Simple-resume-and-works-collection/#/"
				repoURL="https://github.com/iamkyc/Simple-resume-and-works-collection"
				date="2017-10-15"
				/>
			</div>
		);
	}
}

class Memo extends React.Component{
	render(){
		return(
			<div className="memo row-content">
				<div className="row">
					<div className="col-sm-9">
						<h2>{this.props.chMemo}</h2>
						<h5>{this.props.enMemo}</h5>
					</div>
					<div className="col-sm-3">
						<a href={this.props.url}  target="_blank" rel="noopener noreferrer"><button className="sub-button"><i className="fa fa-file-text-o" aria-hidden="true"></i> view</button></a>
					</div>
				</div>
			</div>
		);
	}
}

